@font-face {
  font-style: normal;
  font-family: "Inter";
  font-weight: 400;
  font-display: swap;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  src: local("Inter"), url(./Inter-Regular.woff2) format("woff2"),
    url(./Inter-Regular.woff) format("woff"),
    url(./Inter-Regular.ttf) format("truetype");
}

@font-face {
  font-style: normal;
  font-family: "Inter";
  font-weight: 500;
  font-display: swap;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  src: local("Inter"), url(./Inter-Medium.woff2) format("woff2"),
    url(./Inter-Medium.woff) format("woff"),
    url(./Inter-Medium.ttf) format("truetype");
}

@font-face {
  font-style: normal;
  font-family: "Inter";
  font-weight: 900;
  font-display: swap;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  src: local("Inter"), url(./Inter-Black.woff2) format("woff2"),
    url(./Inter-Black.woff) format("woff"),
    url(./Inter-Black.ttf) format("truetype");
}
